
#banner-app {
  background: #616161;
  padding: 20px;
  height: 195px;
  overflow: hidden;
  position: relative;
}
#banner-app button {
    position: absolute;
    right: 10px;
    top: 13px;
    border: none;
    background: transparent;
    cursor: pointer;
}
#banner-app .wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
#banner-app .wrapper img {
      width: 194px;
      height: 331px;
      position: relative;
      bottom: -5px;
      -ms-flex-negative: 0;
          flex-shrink: 0;
}



