
#download-modal {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 1000;
  top: 0;
  background: rgba(255, 255, 255, 0.97);
}
#download-modal button {
    position: absolute;
    right: 14px;
    top: 17px;
    border: none;
    background: transparent;
    cursor: pointer;
}
#download-modal .wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-top: 15%;
}
#download-modal .wrapper img {
      width: 166px;
      height: 302px;
}
#download-modal .wrapper #sms-form {
      margin-top: 80px;
}
#download-modal .wrapper #sms-form h2, #download-modal .wrapper #sms-form p {
        color: black;
}
#download-modal .wrapper #sms-form input[type=text] {
        background: #EFEFEF;
}



