
#landing-header {
  background: url("../../assets/images/header-background.jpg") no-repeat;
  background-size: cover;
  padding: 20px;
}
#landing-header h1 {
    width: 345px;
}
#landing-header #autocomplete {
    width: 100%;
    display: block;
    margin: 90px auto 0;
    padding: 11px 0 11px 40px;
    font-size: 18px;
    letter-spacing: -0.2px;
    background: white url("../../assets/images/search-icon.svg") no-repeat;
    background-position: 15px 15px;
    -webkit-box-shadow: 0 2px 14px 0 rgba(0, 0, 0, 0.63), 0 0 4px 0 rgba(0, 0, 0, 0.14);
            box-shadow: 0 2px 14px 0 rgba(0, 0, 0, 0.63), 0 0 4px 0 rgba(0, 0, 0, 0.14);
}
#landing-header .awesomplete {
    display: block;
}



