
#category-filters {
  margin-top: 15px;
}
#category-filters button {
    border: 0;
    opacity: 0.3;
    background: transparent;
    font-size: 16px;
    font-weight: bold;
    padding: 0 10px 10px;
    cursor: pointer;
}
#category-filters button.active {
      opacity: 1;
      -webkit-box-shadow: 0 -2px 0 0 #FF0079 inset;
              box-shadow: 0 -2px 0 0 #FF0079 inset;
}
#category-filters button:hover {
      opacity: 1;
}
#category-filters button.authorised {
      padding-left: 35px;
      background: url("../../assets/images/grey-authorised.svg") no-repeat 10px 0px;
}
#category-filters button.authorised.active, #category-filters button.authorised:hover {
        background: url("../../assets/images/authorised.svg") no-repeat 10px 0px;
}
#category-filters button.careful {
      padding-left: 35px;
      background: url("../../assets/images/grey-careful.svg") no-repeat 10px 0px;
}
#category-filters button.careful.active, #category-filters button.careful:hover {
        background: url("../../assets/images/careful.svg") no-repeat 10px 0px;
}
#category-filters button.forbidden {
      padding-left: 35px;
      background: url("../../assets/images/grey-forbidden.svg") no-repeat 10px 0px;
}
#category-filters button.forbidden.active, #category-filters button.forbidden:hover {
        background: url("../../assets/images/forbidden.svg") no-repeat 10px 0px;
}



