
#category-header {
  -webkit-box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.21);
          box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.21);
  position: fixed;
  width: 100%;
  background: white;
}
#category-header header {
    padding: 20px 20px 15px;
}
#category-header header span {
      color: black;
}
#category-header header input[type=search] {
      width: 585px;
      height: 100%;
      padding: 11px 15px 11px 40px;
      font-size: 18px;
      letter-spacing: -0.2px;
      background: #F4F4F4 url("../../assets/images/search-icon.svg") no-repeat;
      background-position: 15px 15px;
}
#category-header .wrapper a {
    text-decoration: none;
}
#category-header .wrapper h1 {
    font-size: 32px;
    color: black;
    letter-spacing: -0.34px;
    margin: 0;
    text-align: left;
    padding-bottom: 15px;
}
#category-header .wrapper h1 span {
      opacity: 0.3;
      margin-left: 5px;
}



