
#food-details {
  padding: 135px 0 100px;
  overflow-y: auto;
}
#food-details p.notice {
    font-size: 12px;
    line-height: 16px;
    opacity: 0.6;
    padding-top: 25px;
}
#food-details .flex {
    margin-top: 30px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
#food-details .flex .category-icon {
      border-radius: 50px;
      width: 92px;
      height: 92px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
#food-details .flex .category-icon img {
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
}
#food-details .flex .category-icon.dairy {
        background: #484291;
}
#food-details .flex .category-icon, #food-details .flex .category-icon.vege_fruits {
        background: #FFDA3A;
}
#food-details .flex .category-icon.fish_seafood {
        background: #007CFF;
}
#food-details .flex .category-icon, #food-details .flex .category-icon.condiments {
        background: #9F57B6;
}
#food-details .flex .category-icon, #food-details .flex .category-icon.drinks {
        background: #99D22A;
}
#food-details .flex .category-icon, #food-details .flex .category-icon.deli {
        background: #FFDA3A;
}
#food-details .flex .category-icon, #food-details .flex .category-icon.deserts {
        background: #99D22A;
}
#food-details .flex .category-icon, #food-details .flex .category-icon.carbs {
        background: #484291;
}
#food-details .flex .category-icon, #food-details .flex .category-icon.plants {
        background: #FFDA3A;
}
#food-details .flex .category-icon, #food-details .flex .category-icon.cooked_food {
        background: #9F57B6;
}
#food-details .flex .category-icon, #food-details .flex .category-icon.meat {
        background: #FFDA3A;
}
#food-details .flex .category-icon, #food-details .flex .category-icon.miscellanious {
        background: #99D22A;
}
#food-details .flex button, #food-details .flex a {
      border: none;
      background: #F4F4F4;
      border-radius: 3px;
      cursor: pointer;
}
#food-details .flex a {
      padding: 19px 20px 11px;
}
#food-details .flex button {
      padding: 12px 20px;
}
#food-details .flex button.bookmark {
        background: #FF0079 url("../../assets/images/white-star.svg") no-repeat 13px 10px;
        color: white;
        font-size: 15px;
        font-weight: bold;
        padding-left: 45px;
}
#food-details .flex .title h2 {
      color: black;
      font-size: 38px;
      font-weight: normal;
}
#food-details .flex .title p {
      padding: 10px 0 10px 27px;
      margin-top: 5px;
      opacity: 1;
}
#food-details .flex .title p.authorised {
        background: url("../../assets/images/authorised.svg") no-repeat 0px 8px;
        color: #99D22A;
}
#food-details .flex .title p.careful {
        background: url("../../assets/images/careful.svg") no-repeat 0px 9px;
        color: #FDAE00;
}
#food-details .flex .title p.forbidden {
        background: url("../../assets/images/forbidden.svg") no-repeat 0px 8px;
        color: #F64848;
}
#food-details .flex .title img {
      border-radius: 50px;
      width: 92px;
      height: 92px;
}
#food-details .links {
    margin-bottom: 30px;
}
#food-details .more {
    padding-bottom: 25px;
    border-bottom: 1px solid rgba(142, 142, 147, 0.1);
}
#food-details .more h3 {
      font-size: 18px;
      font-weight: bold;
}
#food-details .more p {
      opacity: 0.6;
      line-height: 23px;
      margin-top: 2px;
      font-size: 18px;
}
#food-details .more button {
      border: none;
      background: transparent;
      padding: 0;
      color: #007CFF;
      text-decoration: none;
      display: block;
      margin-top: 5px;
      font-size: 18px;
      cursor: pointer;
}
#food-details .info {
    margin-top: 30px;
}



