
#error-page .message {
  padding-top: 135px;
}
#error-page .message img {
    padding-top: 45px;
}
#error-page .message h2 {
    color: black;
    font-size: 32px;
    width: 550px;
    line-height: 36px;
    margin-top: 20px;
    letter-spacing: -0.35px;
}
#error-page .message button {
    border: none;
    border-radius: 3px;
    cursor: pointer;
    padding: 12px 15px;
    background: #FF0079;
    color: white;
    font-size: 15px;
    font-weight: bold;
    letter-spacing: -0.16px;
    margin-top: 20px;
    -webkit-transition: opacity .3s;
    transition: opacity .3s;
}
#error-page .message button:hover {
      opacity: 0.7;
}



