
#category-item .category-icon {
  width: 110px;
  height: 128px;
  border-radius: 6px;
  margin-bottom: 5px;
  -webkit-box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.15);
          box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.15);
  text-align: center;
  position: relative;
}
#category-item .category-icon img {
    position: absolute;
    bottom: 13px;
    left: 10px;
}
#category-item .category-icon:hover {
    -webkit-animation: pulse .5s linear;
            animation: pulse .5s linear;
}
@-webkit-keyframes pulse {
from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
50% {
    -webkit-transform: scale3d(1.03, 1.03, 1.03);
            transform: scale3d(1.03, 1.03, 1.03);
}
to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}
@keyframes pulse {
from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
50% {
    -webkit-transform: scale3d(1.03, 1.03, 1.03);
            transform: scale3d(1.03, 1.03, 1.03);
}
to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}
#category-item .category-icon.dairy {
    background: #484291;
}
#category-item .category-icon, #category-item .category-icon.vege_fruits {
    background: #FFDA3A;
}
#category-item .category-icon.fish_seafood {
    background: #007CFF;
}
#category-item .category-icon, #category-item .category-icon.condiments {
    background: #9F57B6;
}
#category-item .category-icon, #category-item .category-icon.drinks {
    background: #99D22A;
}
#category-item .category-icon, #category-item .category-icon.deli {
    background: #FFDA3A;
}
#category-item .category-icon, #category-item .category-icon.deserts {
    background: #99D22A;
}
#category-item .category-icon, #category-item .category-icon.carbs {
    background: #484291;
}
#category-item .category-icon, #category-item .category-icon.plants {
    background: #FFDA3A;
}
#category-item .category-icon, #category-item .category-icon.cooked_food {
    background: #9F57B6;
}
#category-item .category-icon, #category-item .category-icon.meat {
    background: #FFDA3A;
}
#category-item .category-icon, #category-item .category-icon.miscellanious {
    background: #99D22A;
}
#category-item h3 {
  font-size: 13px;
  color: #8E8E93;
  font-weight: bold;
  line-height: 15px;
}



